<!--
  @auth:lcw
  echarts词云
-->
<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    ref="chartEl"
    :style="{width : params.canvasStyle.width, height: params.canvasStyle.height}"
  ></div>
</template>

<script>
  import * as echarts from "echarts";
  import "echarts-wordcloud/dist/echarts-wordcloud";
  import "echarts-wordcloud/dist/echarts-wordcloud.min";

  export default {
    name: "wordcloud",
    props: {
      params: {
        type: Object,
        default: {
          canvasStyle: {
            width: "1000px",
            height: "600px"
          },
          data: [],
          options: {}
        }
      }
    },
    data() {
      return {
        myChart: null,
      };
    },
    mounted() {
      this.init();
      this.$once('hook:beforeDestroy', () => {
        if (this.interval) {
          clearInterval(this.interval);
        }
        this.myChart && this.myChart.dispose();
      })
    },
    methods: {
      init() {
        if (this.myChart) {
          this.myChart.dispose();
          this.myChart = null;
        }
        if (this.params.data.length === 0) return;
        let propData = [...this.params.data];
        let options = this.params.options;

        this.myChart = echarts.init(this.$refs.chartEl);
        this.option = {
          series: [
            {
              type: "wordCloud",
              //词之间的距离
              gridSize: 16,
              //字的大小范围
              sizeRange: [10, 30],
              //用来调整词的旋转方向，，[0,0]--代表着没有角度，也就是词为水平方向
              // rotationRange: [-45, 0, 45, 90],
              // rotationRange: [ 0,90],
              rotationRange: [0, 0],
              //随机生成字体颜色
              // maskImage: maskImage,
              textStyle: {
                normal: {
                  color:"#c8f5ff"
                },
                emphasis:{
                  color:"#f2684f",
                  fontWeight:"bold"
                }
              },
              //位置相关设置
              left: "center",
              top: "center",
              right: null,
              bottom: null,
              width: "200%",
              height: "200%",
              //数据
              data: propData
            }
          ]
        };
        this.myChart.setOption(this.option);

        let clickCurIndex = -1;
        if(options.isClick){
          this.myChart.on("click", params => {
            let name = params.name;
            if (clickCurIndex !== params.dataIndex) {
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: clickCurIndex
              });
              // 高亮当前图形
              this.myChart.dispatchAction({
                type: "highlight",
                dataIndex: params.dataIndex
              });
              clickCurIndex = params.dataIndex;
              name = params.name;
            } else {
              // 重复点击
              // 取消之前高亮的图形
              this.myChart.dispatchAction({
                type: "downplay",
                dataIndex: clickCurIndex
              });
              name = 'drop';
            }

            this.$emit("canvasClick", name);
          });
        }
      }
    },
    watch: {
      'params.data': {
        handler(newVal, oldVal) {
          this.init();
        },
        deep: true
      }
    }
  }
</script>

